							<img src="../assets/logo.png" alt="品牌logo">
						</div>
						<div class="full-hd-li full-bd__lic">
							<div class="full-bd__licn">梅岭月亮湾红包</div>
							<div class="full-bd__lict">2017-10</div>
						</div>
						<div class="full-hd-li full-bd__lir">1.00元</div>
					</li>
				</ul>
			</div>
		</div>
		<div class="full-bd__f">
