

<template>
  <fullscreen>
  	<div class="full-hd">
  		<router-link class="full-hd__btn" to="/">←</router-link>
  		<span class="full-hd__title">红包详情</span>
  	</div>
  	<div class="full-bd">
		<div class="full-bd__t">
			<div class="full-bd__redradius">
				<div class="full-bd__logo">
					<img src="../assets/logo.png" alt="品牌logo"></div>
			</div>
			<div class="full-bd__n">
				<div class="full-bd__cn">维悟科技</div>
				<div class="full-bd__text">吉祥如意</div>
			</div>
		</div>
		<div class="full-bd__c">
			<div class="full-bd__tip">截至时间：2017-11-11 00:00</div>
			<div class="full-bd__list">
				<ul>
					<li v-for="item in 7">
						<div class="full-hd-li full-bd__liavator">
							<img src="http://vr.yunwucm.com/plugin/comment/avatar/GmMZfibxEqPpHkdRR30cp3AfiaibOR18GXqhmW186WBFyXjj3XYGfDcNSWzEpjicfJNE4zFj9N7U3tD4aH2OqPbWQ2SWjRmzld8w.jpg" alt="">
						</div>
						<div class="full-hd-li full-bd__lic">
							<div class="full-bd__licn">小段微信名称</div>
							<div class="full-bd__lict">2017-10-25 08:00</div>
						</div>
						<div class="full-hd-li full-bd__lir">30.00元</div>
					</li>
				</ul>
			</div>
		</div>
		<div class="full-bd__f">
			<router-link to="/redpack/redpackmy">查看我的红包记录></router-link>
		</div>
  	</div>
  </fullscreen>
</template>

<script>
/*
红包详情
*/
import Fullscreen from '@/components/dialog/fullscreen';

export default {
  data() {
    return {
    }
  },
  methods: {
  },

  components: {
    Fullscreen
  }
}
</script>


<style lang="scss" scoped>
@import '../assets/sass-global.scss';

.full-hd {
    color: #EBCD9A;
    text-align: left;
	padding: 10px;
	background-color:#D85940;
	font-size: 18px;
	letter-spacing: 0.1em;
	.full-hd__btn{
    	color: #EBCD9A;
		padding: 0 10px;
	}
	.full-hd__title{
		border-left: 1px #EBCD9A solid;
		padding-left:15px;
	}
}
.full-bd{
    overflow:auto;
	height: 100%;
	.full-bd__t{
		background-color: #F1F1F1;
		height: 200px;
		.full-bd__redradius{
			height: 70px;
		    background-color: #D85940;
			border-radius: 0px 0px 50% 50% / 10px 10px 70% 70%;
			.full-bd__logo{
				display: inline-block;
				margin-top: 45px;
				img{
					width: 60px;
    				height: 60px;
				}
			}
		}
		.full-bd__n{
			padding: 50px 10px 10px;
			.full-hd__cn{
				font-weight: 700;
			}
			.full-bd__text{
				color:gray;
				font-weight: 600;
			}
		}
	}
	.full-bd__c{
		text-align: left;
		padding:0 15px;
		.full-bd__tip{
			color:gray;
			padding: 10px 0;
		}
		.full-bd__list{
			ul{
				li{
					list-style-type:none;
					border-bottom: 1px #d6d6d6 solid;
					&:last-child{
						border-bottom:none;
					}
					.full-hd-li{
						display: inline-block;
						margin:10px 0px 5px;
						max-height: 50px;
    					overflow: hidden;
					}
					.full-bd__liavator{
						width: 50px;
						img{
							width:48px;
							height:48px;
							border-radius: 3px;
						}
					}
					.full-bd__lic{						
						width: 60%;
						.full-bd__licn{
							overflow: hidden;
							max-height: 25px;
							text-overflow:ellipsis;
							white-space: nowrap;
						}
						.full-bd__lict{
							color:gray;
						}
					}
					.full-bd__lir{					
						width: 20%;
						font-weight:700;
						line-height: 50px;
						text-align: right;
					}
				}
			}
		}
	}	
	.full-bd__f{
		height: 100px;
		padding: 10px;
	}
}
</style>


