import Vue from 'vue'
import Router from 'vue-router'
import redPack from '@/components/redPack'
import readMe from '@/components/readMe'
import redpackdetail from '@/pages/redpackdetail'
import redpackmy from '@/pages/redpackmy'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'redpack',
      component: redPack
    },
    {
      path: '/redpack/readme',
      name: 'redpack/readme',
      component: readMe
    },
    {
      path: '/redpack/redpackdetail',
      name: 'redpack/redpackdetail',
      component: redpackdetail
    },
    {
      path: '/redpack/redpackmy',
      name: 'redpack/redpackmy',
      component: redpackmy
    }
  ]
})



