<template>
  <div id="vueapp">
    <div class="js_dialog" id="readdialog" style="opacity:1;">
      <div class="weui-mask"></div>
      <div class="weui-dialog read-container" id="read-container">
        <div class="read">
          <div class="topcontent">
            <div class="rightclose" @click="closered">
              <a href="javascript:;">+</a>
            </div>
            <div class="read-avatar">
              <router-link to="/">
              <img src="../assets/logo.png" alt="品牌logo">
              </router-link>
            </div>
            <div class="read-text">{{ name }}</div>
            <div class="read-description">{{ msg }}</div>
            <div class="read-qcode"><img src="http://www.yunwucm.com/wp-content/themes/NC-YW/images/code2.jpg" alt=""></div>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
  </div>
</template>
<script>
export default {
  name: 'vueapp',
  data() {
    return {
      openedred: 0, //已打开
      openrotate: 0, //点击开按钮
      name: '维悟科技',
      msg: '商家二维码'
    }
  },
  methods: {
    closered: function() {
      //关闭红包窗
      console.log('关闭');
      alert('您点击了关闭！');
      // $('#readdialog').fadeOut(200);
    },
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$screen-min: 768px;
$screen-mid: 992px;
$screen-max: 1200px;


.read-container {
  max-width: 360px;
  color: #EBCD9A;
  .read {
    min-height: 360px;
    background: #CD533D;
    margin: 0 auto;
    @media screen and (min-width: $screen-min) {
      min-height: 460px;
    }
    .white-text {
      color: white;
    }
    .read-open {
      width: 85px;
      height: 85px;
      cursor: pointer;
      line-height: 85px;
      border: 1px solid #EBCD9A;
      background-color: #EBCD9A;
      border-radius: 50%;
      color: #303030;
      font-size: 32px;
      display: inline-block;
      margin-top: -50px;
      box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.2);
    }
    .rotate {
      -webkit-animation: anim .6s infinite alternate;
      -ms-animation: anim .6s infinite alternate;
      animation: anim .6s infinite alternate;
    }
    .read-bottom {
      font-size: 12px;
      position: absolute;
      bottom: 20px;
      width: 100%;
    }
    .topcontent {
      .rightclose {
        text-align: left;
        line-height: 1em;
        padding: 5px 0 0 5px;
        a {
          display: inline-block;
          color: rgba(0, 0, 0, 0.5);
          -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
          font-size: 2em;
        }
      }
      .read-avatar {
        padding-top: 10px;
        img {
          /*border: 1px solid #BD503A;
                    border-radius: 50%;*/
          width: 40px;
          height: 40px;
          overflow: hidden;
        }
      }
      .read-text {
        font-size: 13px;
      }
      .read-description {
        letter-spacing: 0.1em;
        padding: 15px;
        padding: 10px;
        font-size: 18px;
        font-weight: 600;
      }
      .read-qcode{
        img{
          width:120px;
          height:120px;
          border-radius:5px;
        }
      }
    }
  }
}

</style>



