<template>
  <div id="vueapp">
    <div class="js_dialog" id="redpackdialog" style="opacity:1;">
      <div class="weui-mask"></div>
      <div class="weui-dialog redpack-container" id="redpack-container">
        <div class="redpack">
          <div class="topcontent">
            <div class="rightclose" @click="closered">
              <a href="javascript:;">+</a>
            </div>
            <div class="redpack-avatar">
              <router-link to="/redpack/readme">
                <img src="../assets/logo.png" alt="品牌logo">
              </router-link>
            </div>
            <div class="redpack-text">{{ name }}</div>
            <div class="redpack-description">{{ msg }}</div>
          </div>
          <div v-if="!openedred" v-bind:class="['redpack-open',{ rotate:openrotate}]" @click="openred"><span>抢</span></div>
          <div class="redpack-bottom">
            <router-link to="/redpack/redpackdetail">
              看看大家的手气 》
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
  </div>
</template>
<script>
export default {
  name: 'vueapp',
  data() {
    return {
      openedred: 0, //已打开
      openrotate: 0, //点击开按钮
      name: '维悟科技',
      msg: '恭喜发财 大吉大利'
    }
  },
  methods: {
    closered: function() {
      //关闭红包窗
      console.log('关闭');
      alert('您点击了关闭！');
      // $('#redpackdialog').fadeOut(200);
    },
    openred: function() {
      //加入旋转动画 发送ajax
      this.openrotate = 1;
      var _this = this;
      setTimeout(function() {
        _this.openrotate = 0;
        _this.openedred = 1;
        _this.msg = '手慢了，该红包已派完了';
      }, 2000);
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/sass-global.scss';

.redpack-container {
  max-width: 360px;
  color: #EBCD9A;
  .redpack {
    min-height: 360px;
    background: #CD533D;
    margin: 0 auto;
    @media screen and (min-width: $screen-min) {
      min-height: 460px;
    }
    .white-text {
      color: white;
    }
    .redpack-open {
      width: 85px;
      height: 85px;
      cursor: pointer;
      line-height: 85px;
      border: 1px solid #EBCD9A;
      background-color: #EBCD9A;
      border-radius: 50%;
      color: #303030;
      font-size: 32px;
      display: inline-block;
      margin-top: -50px;
      box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.2);
    }
    #redpack-opened {
      width: 85px;
      height: 85px;
      font-weight: 700;
      line-height: 85px; //border: 1px solid #FFA73A;
      //background-color: #FFA73A;
      border-radius: 50%;
      color: #fff;
      font-size: 20px;
      display: inline-block;
      margin-top: -50px; //box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.2);
    }
    .rotate {
      -webkit-animation: anim .6s infinite alternate;
      -ms-animation: anim .6s infinite alternate;
      animation: anim .6s infinite alternate;
    }
    .redpack-bottom {
      font-size: 12px;
      position: absolute;
      bottom: 20px;
      width: 100%;
    }
    .topcontent-open {
      height: 70px;
      min-height: 70px;
      border: 1px solid #BD503A;
      background-color: #BD503A;
      border-radius: 10px 10px 50% 50% / 10px 10px 22% 22%;
      box-shadow: 0px 4px 0px -1px rgba(0, 0, 0, 0.1);
      transition: 1s ease-in-out;
    }
    .topcontent {
      min-height: 240px;
      border: 1px solid #D85940;
      background-color: #D85940;
      border-radius: 10px 10px 50% 50% / 10px 10px 20% 20%;
      box-shadow: 0px 4px 0px -1px rgba(0, 0, 0, 0.2);
      margin-bottom: -10px;
      .rightclose {
        text-align: left;
        line-height: 1em;
        padding: 5px 0 0 5px;
        a {
          display: inline-block;
          color: rgba(0, 0, 0, 0.5);
          -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
          font-size: 2em;
        }
      }
      .redpack-avatar {
        padding-top: 10px;
        img {
          /*border: 1px solid #BD503A;
                    border-radius: 50%;*/
          width: 40px;
          height: 40px;
          overflow: hidden;
        }
      }
      .redpack-text {
        font-size: 13px;
      }
      .redpack-description {
        letter-spacing: 0.1em;
        padding: 15px;
        padding: 10px;
        font-size: 18px;
        font-weight: 600;
      }
    }
  }
}


@-webkit-keyframes anim {
  from {
    -webkit-transform: rotateY(180deg);
  }
  to {
    -webkit-transform: rotateY(360deg);
  }
}

@-ms-keyframes anim {
  from {
    -ms-transform: rotateY(180deg);
  }
  to {
    -ms-transform: rotateY(360deg);
  }
}

@keyframes anim {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

</style>



