<template>
  <div class="full-dialog ">
      <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
  }
}
</script>


<style lang="scss" >
@import '../../assets/sass-global.scss';

.full-dialog {
    position: fixed;
    z-index: 5000;
    width: 100%;
    height: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    background-color: #fff;
    text-align: center;
    overflow:hidden;
}
</style>


